
document.addEventListener('DOMContentLoaded', function(){
   evenlisteners();
   modoOscuro();
   alerta();
});

  const nav=document.querySelector('.header__nav');
  const menu=document.querySelector('.header__hamburguesa');
  const dark=document.querySelector('.header__dark');
  const body=document.querySelector('body');
  const mostrar=document.querySelector('.ver-contraseña');
  const metodoContacto= document.querySelectorAll('input[name="contacto[contacto]"]');



function evenlisteners(){

  if (mostrar){
    mostrar.addEventListener('mouseover',toggleContraseña );
    mostrar.addEventListener('mouseout',toggleContraseña );
  }

  if(menu){
    menu.addEventListener('click', mostrarnav);
  }


  metodoContacto.forEach (input=> input.addEventListener('click', mostrarTipo))



}



function mostrarnav() {
    if(!nav.classList.contains('mostrar')){
      nav.classList.add("mostrar");
    }
    else {
      nav.classList.remove('mostrar');
    }
   
}


function modoOscuro() {

  const preferenciaSistema=window.matchMedia('(prefers-color-scheme: dark)');

  if(preferenciaSistema.matches){
    document.body.classList.add('modo__oscuro');
  }
  else {
    document.body.classList.remove('modo__oscuro');
  }

  preferenciaSistema.addEventListener('change', ()=>{
    if(preferenciaSistema.matches){
      document.body.classList.add('modo__oscuro');
    }
    else {
      document.body.classList.remove('modo__oscuro');
    }
  });


  /*  if (!body.classList.contains('modo__oscuro')){
      body.classList.add("modo__oscuro");
     
    }
    else {
      body.classList.remove("modo__oscuro");
      
    } */

    // otra opción más sencilla
    dark.addEventListener('click', function(){
      document.body.classList.toggle('modo__oscuro');
    });
  
}


// mostrar la alerta de corecto cuando agregas una nueva propiedad (VER)

function alerta(){
 cambios=document.querySelector(".cambios");
  if(cambios){
    setTimeout(()=>{
      cambios.style.display='none';
    },5000);
  
  }
 

}

function toggleContraseña() {

  const passwordInput = document.querySelector("#contraseña");
  if (passwordInput.type === "password") {
    passwordInput.type = "text";
  } else {
    passwordInput.type = "password";
  }
}


function mostrarTipo(e){

  const desplegar= document.querySelector('#contacto');
  const mostrarOno= document.querySelector('#contacto-telefono');

  if(e.target.value=== 'telefono'){
    desplegar.innerHTML= `
    <div class="form__campo">
    <label for="tel" class="form__label">Número de Teléfono</label>
    <input id="tel" type="tel" class="form__input" placeholder="Tu Teléfono" name="contacto[telefono]" required>
    </div> 

     
      <div class="form__flex">
      <p class="form__p">Elija la hora y fecha para que te contactemos </p>
    
      <div class="form__flex--direction">
        <div class="form__campo ">
          <label for="" class="form__label">fecha:</label>
          <input  id="telefono" type="date" class="form__input" name="contacto[fecha] " required>
        </div>
        
        <div  class="form__campo ">
          <label for="" class="form__label">hora:</label>
          <input id="telefono" type="time" class="form__input" name="contacto[hora]" required>
      </div >
      </div>
     </div>
    `;
    

  } else {
    desplegar.innerHTML= `
    <div class="form__campo">
    <label for="email" class="form__label"> E-mail contacto</label>
    <input id="email" type="email" class="form__input" placeholder="Tu E-Mail" name="contacto[email]" required>
    </div> <!--campo-->
    `;

    
  } 
 

  
}














